export default {
	data() {
		return {
			list:[
				// {
				// 	content:'test1'
				// },
				// {
				// 	content:'test2'
				// }
			],
			active:false,
			value:'',
			activeIndex:0,
			text:'全部',
			textShow:false
			
		}
	},
	onLoad() {

	},
	computed:{
		
		listData(){
			let list=JSON.parse(JSON.stringify(this.list))
			let newList=[]
			if(this.activeIndex===0){
				this.text='全部'
				return list
			}
			if(this.activeIndex===1){
				this.text='待办'
				list.forEach(item=>{
					if(!item.checked){
						newList.push(item)
					}
				})
				return newList
			}
			if(this.activeIndex===2){
				this.text='已完成'
				list.forEach(item=>{
					if(item.checked){
						newList.push(item)
					}
				})
				return newList
			}
			
		}
	},
	methods: {
		//打开输入框
		create(){
			if(this.active){
				this.close()
			}else{
				this.open()
			}
		},
		//打开动画
		open(){
			this.active=true
			this.$nextTick(()=>{
				setTimeout(()=>{
					this.textShow=true
				},50)
			})
		},
		//关闭动画
		close(){
			this.active=false
			this.$nextTick(()=>{
				setTimeout(()=>{
					this.textShow=false
				},50)
			})
		},
		//添加任务
		add(){
			if(this.value===''){
				uni.showToast({
					title:"请输入内容",
					icon:none,
				})
				return
			}
			this.list.unshift({
				id:'id'+new Date().getTime(),
				content:this.value,
				checked:false
			})
			this.value=''
			this.active=false
			this.close()
		},
		finish(id){
			let index=this.list.findIndex((item=>item.id===id))
			this.list[index].checked=!this.list[index].checked
		
		},
		tab(index){
			this.activeIndex=index
		}
	}
}
